import React from 'react'
import { Person, Column } from '../../types'

import TableItem from './TableItem'

type Props = {
  persons: Person[],
  onSort: (columnKey: Column) => void
}

const Table: React.FC<Props> = ({ persons, onSort }) => {
  return (
    <table>
      <thead>
        <tr>
          <th onClick={() => onSort('id')} >Id</th>
          <th onClick={() => onSort('firstName')} >First Name</th>
          <th onClick={() => onSort('lastName')} >Last Name</th>
          <th onClick={() => onSort('email')} >Email</th>
          <th onClick={() => onSort('phone')} >Phone</th>
        </tr>
      </thead>
      <tbody>
        {persons &&
          persons.map(person => <TableItem key={person.id + person.phone} {...person} />)
        }
      </tbody>
    </table>
  )
}

export default Table